.paginator {
    margin: 30px;
    width: 1000px;
}

.pageNumber {
    color: #fff;
    padding: 10px;
    border: 1px solid #fff;
}

.paginator span {
    cursor: pointer;
    width: 20px;
    height: 20px;
    padding: 5px;
}

.paginator span+span {
    margin: 10px;
}

.pageNumber.selectedPage {
    font-weight: bold;
    border:2px solid #04c400;
    color: #04c400;
    font-weight: normal;
}